html {
    height: 100%;
}

body {
    background: #7092be url('paint_job.png') no-repeat center center fixed;
    background-size: 150% auto;
    position: fixed;
    padding: 0px;
    margin: 0px;
    width: 100%;
    height: 100%;
    font: normal 14px/1.618em;
    font-family: 'Saira Condensed', sans-serif;
    -webkit-font-smoothing: antialiased;
}

h1, h2, h3 {
    font-weight: 400;
    margin: 0px 0px 0px 0px;
}
h1 {
    font-size: 24px;
}
h2 {
    font-size: 16px;
}
h3 {
  color: #2e64ab;
  font-size: 17px;
}
p {
    margin: 0px;
    text-align: centre;
    font-size: 17px;
}
a {
  margin: 0px;
  text-align: center;
  font-size: 18px;
  color: #2e64ab;
  cursor: pointer;
  text-decoration: none;
}

a:hover{
  color: rgb(45, 128, 107);
}

#btn-download{
  font-family: inherit;
  font-size: 17px;
  text-decoration: none;
  border: none;
  background: none;
  cursor: pointer;
  color: #2e64ab;
}

#btn-download:hover{
  color: rgb(45, 128, 107);
}

#download{
  display: flex;
  justify-content: center;
 
}

.profile-card {
    background: #7092be;
    width: 34px;
    height: 34px;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 2;
    overflow: hidden;
    opacity: 0;
    margin-top: 70px;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-border-radius: 50%;
    border-radius: 50%;
    -webkit-animation: init 0.5s 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, moveDown 1s 0.8s cubic-bezier(0.6, -0.28, 0.735, 0.045) forwards, moveUp 1s 1.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards, materia_landscape 0.5s 2.7s cubic-bezier(0.86, 0, 0.07, 1) forwards;
    animation: init 0.5s 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, moveDown 1s 0.8s cubic-bezier(0.6, -0.28, 0.735, 0.045) forwards, moveUp 1s 1.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards, materia_landscape 0.5s 2.7s cubic-bezier(0.86, 0, 0.07, 1) forwards;
}
.profile-card header {
    width: 179px;
    height: 180px;
    /* padding: 40px 20px 30px 20px; */
    display: inline-block;
    float: left;
    border-right: 2px dashed #EEEEEE;
    background: #FFFFFF;
    color: #000000;
    margin-top: 50px;
    opacity: 0;
    text-align: center;
    -webkit-animation: moveIn 1s 3.1s ease forwards;
    animation: moveIn 1s 3.1s ease forwards;
}
.profile-card header h1 {
    color: #2e64ab;
    font-weight: bold;
    font-size: 50px;

}
.profile-card header h2 {
  color: #30507a;
  font-size: 24px;

}

.profile-card .profile-bio {
    width: 175px;        
    height: 180px;
    display: inline-block;
    float: right;
    padding: 10px 20px 30px 20px;
    background: #FFFFFF;
    color: #333333;
    margin-top: 10px;
    text-align: center;
    opacity: 0;
    -webkit-animation: moveIn 1s 3.1s ease forwards;
    animation: moveIn 1s 3.1s ease forwards;

    
}

@media screen and (min-aspect-ratio: 4/3) {
    body {
        background-size: 100% auto;
    }
    body:before {
        width: 0px;
    }
    @-webkit-keyframes puff {
        0% {
            top: 100%;
            width: 0px;
            padding-bottom: 0px;
        }
        100% {
            top: 50%;
            width: 100%;
            padding-bottom: 100%;
        }   
    }
    @keyframes puff {
        0% {
            top: 100%;
            width: 0px;
            padding-bottom: 0px;
        }
        100% {
            top: 50%;
            width: 100%;
            padding-bottom: 100%;
        }   
    }
}

@media screen and (min-height: 400px){
    .profile-card {
        -webkit-animation: init 0.5s 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, moveDown 1s 0.8s cubic-bezier(0.6, -0.28, 0.735, 0.045) forwards, moveUp 1s 1.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards, materia_portrait 0.5s 2.7s cubic-bezier(0.86, 0, 0.07, 1) forwards;
        animation: init 0.5s 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, moveDown 1s 0.8s cubic-bezier(0.6, -0.28, 0.735, 0.045) forwards, moveUp 1s 1.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards, materia_portrait 0.5s 2.7s cubic-bezier(0.86, 0, 0.07, 1) forwards;
    }
    .profile-card header {
        width: auto;
        height: auto;
        /* padding: 30px 20px; */
        display: block;
        float: none;
        border-right: none;
    }
    .profile-card .profile-bio {
        width: auto;
        height: auto;
        padding: 10px 20px 30px 20px;
        display: block;
        float: none; 
    }
}
@media screen and (min-aspect-ratio: 4/3) {
    body {
        background-size: 100% auto;
    }
    body:before {
        width: 0px;
        -webkit-animation: puff_landscape 0.5s 1.8s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, borderRadius 0.2s 2.3s linear forwards;
        animation: puff_landscape 0.5s 1.8s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, borderRadius 0.2s 2.3s linear forwards;
    }
}
@-webkit-keyframes init {
    0% {
        width: 0px;
        height: 0px;
    }
    100% {
        width: 34px;
        height: 34px;
        margin-top: 0px;
        opacity: 1;
    }   
}
@keyframes init {
    0% {
        width: 0px;
        height: 0px;
    }
    100% {
        width: 34px;
        height: 34px;
        margin-top: 0px;
        opacity: 1;
    }   
}

@-webkit-keyframes borderRadius {
    0% {
        -webkit-border-radius: 50%;
    }
    100% {
        -webkit-border-radius: 0px;
    }   
}
@keyframes borderRadius {
    0% {
        -webkit-border-radius: 50%;
    }
    100% {
        border-radius: 0px;
    }   
}

@-webkit-keyframes moveUp {
    0% {
        background: #7092be;
        top: 100%;
    }
    50% {
       top: 40%;
    }
    100% {
       top: 50%;
       background: red;
    }   
}
@keyframes moveUp {
    0% {
        background: #7092be;
        top: 100%;
    }
    50% {
       top: 40%;
    }
    100% {
       top: 50%;
       background: red;
    }   
}
@-webkit-keyframes materia_landscape {
    0% {
        background: #FFFFFF;
    }
    50% {
        -webkit-border-radius: 4px;
    }
    100% {
        width: 540px;
        height: 400px;
        background: #FFFFFF;
        -webkit-border-radius: 4px;
    }
}
@keyframes materia_landscape {
    0% {
        background: #FFFFFF;
    }
    50% {
        border-radius: 4px;
    }
    100% {
        width: 540px;
        height: 400px;
        background: #FFFFFF;
        border-radius: 4px;
    }
}
@-webkit-keyframes materia_portrait {
    0% {
        background: #FFFFFF;
    }
    50% {
        -webkit-border-radius: 4px;
    }
    100% {
        width: 400px;
        height: 540px;
        background: #FFFFFF;
        -webkit-border-radius: 4px;
    }
}
@keyframes materia_portrait {
    0% {
        background: #FFFFFF;
    }
    50% {
        border-radius: 4px;
    }
    100% {
        width: 400px;
        height: 540px;
        background: hsl(0, 0%, 100%);
        border-radius: 4px;
    }
}
@-webkit-keyframes moveIn {
    0% {
        margin-top: 50px;
        opacity: 0;
    }
    100% { 
        opacity: 1;
        margin-top: 10px;
    }   
}
@keyframes moveIn {
    0% {
        margin-top: 50px;
        opacity: 0;
    }
    100% { 
        opacity: 1;
        margin-top: 10px;
    }   
}
@-webkit-keyframes scaleIn {
    0% {
        -webkit-transform: scale(0);
    }
    100% { 
        -webkit-transform: scale(1);
    }   
}
@keyframes scaleIn {
    0% {
        transform: scale(0);
    }
    100% { 
        transform: scale(1);
    }   
}
